import { useState, useEffect } from "react";
import { Helmet } from "react-helmet";
import {
  Container,
  Col,
  Row,
  Button,
  Stack,
  Image,
  Form,
  Modal,
  InputGroup,
  Carousel
} from "react-bootstrap";
import './virtualWorkshop.css'
import CoursesDataServices from '../../Services/CoursesServices'
import activity from "../../assets/actividad.png";
import { useParams } from "react-router-dom";
import TGV1 from "../../assets/TGV1.png"
import TGV2 from "../../assets/TGV2.png"
import TGV3 from "../../assets/TGV3.png"
import moment from "moment";

const  CursosVirtuales= ()=>{

    const [index, setIndex] = useState(0);
    const [courses, setCourse] = useState([])


    // Funcin del Carousel de bootstrap
    const handleSelect = (selectedIndex, e) => {
        setIndex(selectedIndex);
    };

    const retrieveCourse = () => {

    CoursesDataServices.getAllCourses()
      .then((response) => {
        setCourse(response.data.courses)
      })
      .catch(err => console.log(err))
  }

  useEffect(() => {
    retrieveCourse()
  }, [])

  // Modal
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  //   Validacin

  const [validated, setValidated] = useState(false);

  const handleSubmit = (event) => {
    const form = event.currentTarget;
    if (form.checkValidity() === false) {
      event.preventDefault();
      event.stopPropagation();
    }

    setValidated(true);
  };

    return(
        <>
         <Helmet>
      <title>Silvia Iurman - Talleres Virtuales</title>
      <meta name="description" content="Talleres virtuales de Silvia Iurman - Nuevas Constelaciones Familiares Argentina"/>
    </Helmet>
    <Container fluid>
        <h2>Talleres Virtuales</h2>
      </Container>
      {courses.map((course) =>( course._id === '63d2d475dc2d95cfd1095e83' ? 
      <Container>
        <Row>
          <Col lg={6} sm={12}>

            <Carousel activeIndex={index} onSelect={handleSelect}>
              <Carousel.Item>
                <img
                  className="d-block w-100"
                  src={TGV1}
                  alt="First slide"
                />
              </Carousel.Item>
              <Carousel.Item>
                <img
                  className="d-block w-100"
                  src={TGV2}
                  alt="Second slide"
                />


              </Carousel.Item>
              <Carousel.Item>
                <img
                  className="d-block w-100"
                  src={TGV3}
                  alt="Third slide"
                />
              </Carousel.Item>
            </Carousel>
            {/* <Image className="img-fluid" src={activity} alt="" /> */}
          </Col>

          <Col lg={6} sm={12}>
            <h3
              style={{
                backgroundColor: "#ffffff",
                color: "#9d6b6c",
                textAlign: "left",
              }}
            >
              Prximo taller: {moment(course.day).format(
                              "DD/MM/YYYY"
                            )} {course.hour}
            </h3>

            <p>
            <b>Los talleres de constelaciones familiares</b> son un espacio de encuentro grupal, en el marco de la filosofa de Bert Hellinger, donde la persona trabaja por medio de constelaciones grupales, constelaciones en paralelo, ejercicios sistmicos (o constelatorios), sanacin cuntica, visualizacin activa de Jung. Se realiza una presentacin del tema a la luz de las enseanzas de Hellinger, de manera que comience a moverse la sanacin desde nuevas comprensiones y tomas de conciencia, que continuarn a lo largo del taller. El objetivo es que todos hagan su trabajo desde el lugar que el campo o fuerza mayor quiera para cada uno. La experiencia demuestra que los efectos de la participacin en un marco grupal, son inmediatos y llevan a la persona a dar su prximo paso esencial. Esto lo demuestra la existencia de la resonancia mrfica de la que da cuenta la biologa y la cuntica. Se entra a la labor desde un sistema integrativo donde, en distintos momentos, se refuerzan el trabajo con distintos cuerpos: fsico, mental y emocional incluyendo un momento somtico que mueve vestigios de trauma. El concepto de masa crtica, que cuantos ms somos en una misma labor, ms rpido inclina la balanza hacia el cambio y la transformacin, es en elemento que siempre se recuerda. Entonces, estos talleres resultan espacios de sanacin, transformacin, liberacin y evolucin que se manifiesta en la participacin activa de personas que eligen hacer su proceso en cada reunin.
            </p>
          </Col>
        </Row>
        <br />
        <Stack gap={2} className="col-md-5 mx-auto">
          <Button
            variant="secondary"
            style={{ backgroundColor: "#9d6b6c" }}
            size="lg"
            onClick={handleShow}
          >
            Inscribite
          </Button>
          <br />
        </Stack>
        <Modal show={show} onHide={handleClose}>
          <Modal.Header closeButton>
            <Modal.Title>Inscripcin</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <Form noValidate validated={validated} onSubmit={handleSubmit}>
              {/* <Form> */}
              <Row>
                <Col>
                  <Form.Group className="mb-3" controlId="name">
                    <Form.Label>Nombre</Form.Label>
                    <Form.Control required type="firstName" autoFocus />
                    <Form.Control.Feedback>Correcto!</Form.Control.Feedback>
                  </Form.Group>
                </Col>

                <Col>
                  <Form.Group className="mb-3" controlId="lastName">
                    <Form.Label>Apellido</Form.Label>
                    <Form.Control type="lastName" required />
                    <Form.Control.Feedback>Correcto!</Form.Control.Feedback>
                  </Form.Group>
                </Col>
              </Row>
              {/* </Form> */}
              {/* <Form> */}
              <Row>
                <Col>
                  <Form.Group className="mb-3" controlId="country">
                    <Form.Label>Pas</Form.Label>
                    <Form.Control type="country" required />
                    <Form.Control.Feedback>Correcto!</Form.Control.Feedback>
                  </Form.Group>
                </Col>

                <Col>
                  <Form.Group className="mb-3" controlId="birthday">
                    <Form.Label>Fecha de nacimiento</Form.Label>
                    <Form.Control type="date" required />
                    <Form.Control.Feedback>Correcto!</Form.Control.Feedback>
                  </Form.Group>
                </Col>
              </Row>
              {/* </Form> */}

              {/* <Form> */}
              <Form.Group className="mb-3" controlId="email">
                <Form.Label>Correo</Form.Label>
                <Form.Control type="email" required />
                <Form.Control.Feedback>Correcto!</Form.Control.Feedback>
              </Form.Group>
              {/* </Form> */}

              {/* <Form> */}
              <Form.Group className="mb-3" controlId="phone">
                <Form.Label> Telfono </Form.Label>
                <Form.Control type="phone" required></Form.Control>
                <Form.Control.Feedback>Correcto!</Form.Control.Feedback>
              </Form.Group>

              <Form.Group className="mb-3">
                <Form.Check
                  required
                  label="Acepto los trminos y condiciones"
                  feedback="Para continuar debe aceptar los trminos y condiciones."
                  feedbackType="invalid"
                />
              </Form.Group>

              <Button variant="secondary" onClick={handleClose} style={{ marginRight: "10px", position: "end" }}>
                Cerrar
              </Button>
              <Button variant="primary" type="submit">
                Enviar
              </Button>
              {/* </Form> */}
            </Form>
          </Modal.Body>
        </Modal>
      </Container>
      : 'No se encontr el curso'))}
        </>
    )
}

export default CursosVirtuales